@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&display=swap);
* {
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}



.about{
    margin-top: 80px
}

.notFound{
    margin-top: 120px;
    padding-left: 30px;
}

.notFound__image{
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    top:0;
    left: 0;
}



.post {
    position: relative;
    padding: 5px 10px;
    border: none;
    border-radius: 12px;
    width: 100%;
    height: 70px;
    margin: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    outline: none;
    z-index: 3;
    box-shadow: 5px 5px 5px black;
    transition: all 0.5s;
}

.image-wrapper {
    position: absolute;
    top: -15px;
    right: -25px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
}

.post:hover{
    box-shadow: none;
    transform: translateY(3px);
    opacity: 0.8;
}


.image {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.heading {
    position: relative;
    padding-right: 20%;
    z-index: 5;
    display: flex;
    font-size: 20px;
    justify-content: center;
    transition: color, opacity 0.2s;
    /* flex-wrap: wrap */
}
.ScrollUp{
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 3333333;
    border:none;
    outline: none;
    transition: all 0.2s;
    cursor: pointer;
}
.ScrollUp:hover{
    opacity: 0.8;
}
.input-wrapper{
    position: relative;
    z-index: 2;
}
.input{
    width: 100%;
}
.input__button{
    position: absolute;
    right: 0;
    top: 0;
}
* {
    font-family: 'Goldman', cursive;
}

.hero-wrapper {
    margin-top: 80px;
}

.hero {
    position: relative;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* border: 1px solid black; */
    border-radius: 15px;
    padding: 20px 40px;
    margin: 5px 0;
    overflow: hidden;
    z-index: 1;
}

.hero__text {
    position: relative;
    z-index: 2;
}

.title {
    text-align: center;
    font-size: 28px;
    font-weight: 700;
    text-transform: capitalize;
}

.body {
    line-height: 200%;
    font-size: 20px;
    text-shadow: 1px 1px 1px white;
}

.hero__comments {
    z-index: 2;
    border-top: 1px dashed black;
}

.button__back {
    display: block;
    padding: 5px 10px;
    border: none;
    border-radius: 6px;
    background-color: rgb(131, 131, 131);
    color: white;
    font-size: 25px;
    font-weight: 700;
    transition: opacity 0.2s;
    cursor: pointer;
    margin: 5px
}

.button__back:hover {
    opacity: 0.8;
}

.background-image {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-position: center;
    background-repeat: no-repeat;
    /* Do not repeat the image */
    background-size: cover;
    /* Resize the background image to cover the entire container */
    z-index: -1;
}

.overlay {
    position: absolute;
    left: 0;
    top: 0;
    background-color: rgba(255, 255, 255, 0.6);
    width: 100%;
    height: 100%;
    z-index: 1;
}

.prev {
    position: absolute;
    left: 0;
    top: 0;
    width: 35px;
    height: 100%;
    z-index: 4;
    background-color: white;
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    transition: opacity background-color 0.2s;
}

.prev:hover {
    opacity: 0.2;
    background-color: rgb(163, 163, 163);
}

.next {
    position: absolute;
    right: 0;
    top: 0;
    width: 35px;
    height: 100%;
    z-index: 4;
    background-color: white;
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    transition: opacity background-color 0.2s;
}

.next:hover {
    opacity: 0.2;
    background-color: rgb(163, 163, 163);
}

.arrow {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 50%;
    margin-top: -15px;
    border: 3px solid #c5c5c5;
    border-right: none;
    border-bottom: none;
    z-index: 2;
    cursor: pointer;
    background-color: transparent;
    transition: opacity 0.2s;
    outline: none;
}

.arrow:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.arrow-prev {
    transform: rotate(-45deg);
    left: 10px;
}

.arrow-next {
    transform: rotate(-225deg);
    right: 10px;
}

.loader-wrapper{
    position: absolute;
    left: 0;
    top:0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader {
    position: absolute;
    border: 33px solid #adabab;
    border-radius: 50%;
    border-top: 33px solid #3d3d3d;
    width: 200px;
    height: 200px;
    animation: spin 1s linear infinite;
    z-index: -1;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    40% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
.gallery-wrapper{
    position: relative;
    width: 100%;
    /* padding: 20px; */
    padding-top: 100%;
    margin: 10px;
    border: none;
    outline: none;
    cursor: pointer;
}

.gallery__image{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;

}
.gallery__big{
    transform: scale(0);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    transition: all 0.5s;
}

.gallery__big-active{
    transform: scale(1);
}
.gallery__big-image-wrapper{
    position: relative;
    margin: 0 auto;
    width: 700px;
    height: 700px;
    background-color: white;
}
.gallery__big-image{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top:0;
    cursor: pointer;
    transition: all 0.1s;
}
.gallery__big-image:hover{
    opacity: 0.95;
}
.navBar-wrapper{
    background-color: rgb(131, 131, 131);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    z-index: 999;
    padding-left: 100px;
   
}
.navBar__image{
    width: 50px;
    height: 50px;
}
.navBar{
    display: flex;
    width: 100%;
    /* text-align: center; */
    align-items: center;
    justify-content: space-evenly;
}

.navBar__link{
    color: white;
    text-decoration: none;
    font-weight: 400;
    font-size: 30px;
    padding: 5px 10px;
    transition: opacity, color 0.2s;
}
.active{
    /* font-size: 32px; */
    /* font-weight: 700; */
    color: rgb(0, 0, 0);
}


.navBar__link:hover{
    opacity: 0.8;
}
